import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import jsonp from 'jsonp';


class App extends React.Component {

	state = {
		events: [],
		token: '',
		data: []
	};
	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppAccessTokenReceived':
					alert(e.detail.type, e.detail.data.scope); 
					this.setState({ token: e.detail.data.access_token})
					break;
				case 'VKWebAppCallAPIAPIMethodResult':
					this.setState({ data: e.detail.data.response });
					break;
				case 'VKWebAppCallAPIMethodFailed':
					console.log(e);
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 7087022, "scope": "friends, messages"});
	}
	click = () => {
		connect.send("VKWebAppCallAPIMethod", {
			"method": "messages.getHistoryAttachments", 
			"request_id": "32test", 
			"params": {
				"peer_id": "75479717",
				"media_type": "photo",
				 "v":"5.101", 
				 "access_token": this.state.token
		}});
	}

	render() {
		console.log(this.state);
		return (
			<div>
				{this.state.token}
				<button onClick={this.click}>
					Click
				</button>
			</div>
		);
	}
}

export default App;
